<script>
export default {
  name: "Footer",
  data() {
    return {
      allPageMusic:
        "https://d1kjtx52rxv2sn.cloudfront.net/website/music/music-global.wav",
      buyPageMusic:
        "https://d1kjtx52rxv2sn.cloudfront.net/website/music/music-buy.wav",
      audioState: false,
    };
  },
  watch: {
    "$route.path": {
      handler(path) {
        if (path === "/buy") {
          this.$refs.audioPlayer.src = this.buyPageMusic;
        } else {
          this.$refs.audioPlayer.src = this.allPageMusic;
        }
      },
      deep: true,
    },
  },

  methods: {
    audioStateToggle() {
      console.log(this.$refs.audioPlayer.readyState);
      if (this.$refs.audioPlayer.readyState === 4) {
        if (this.$refs.audioPlayer.paused) {
          this.$refs.audioPlayer.play();
          this.audioState = true;
        } else {
          this.$refs.audioPlayer.pause();
          this.audioState = false;
        }
      }
    },
  },
};
</script>

