<script>
import '@/assets/css/product.css'
import { WOW } from 'wowjs'
export default {
  components: {},
  data() {
    return {
      loading: false,
      endTime: this.getTime('2021-01-01 11:00:00'),
      startTime: this.getTime(),
      clock: undefined,
    }
  },
  computed: {
    endTimeTitle() {
      // 11:00 AM Jan. 1st 2022 UTC+8
      if (!this.$moment.isMoment(this.endTime)) return ''
      if (this.endTime.format('H') > 12) {
        return `${this.endTime.format('hh:mm')} PM ${this.endTime.format(
          'MMM'
        )}. ${this.endTime.format('Do')} ${this.endTime.format('YYYY')} UTC+8`
      } else {
        return `${this.endTime.format('hh:mm')} AM ${this.endTime.format(
          'MMM'
        )}. ${this.endTime.format('Do')} ${this.endTime.format('YYYY')} UTC+8`
      }
    },
    countdown() {
      let startTime = this.$moment(this.startTime),
        endTime = this.$moment(this.endTime),
        du = this.$moment.duration(endTime - startTime, 'ms')
      if (endTime.isAfter(startTime)) {
        return {
          years: du.get('years').toString().padStart(2, 0),
          months: du.get('months').toString().padStart(2, 0),
          days: du.get('days').toString().padStart(2, 0),
          hours: du.get('hours').toString().padStart(2, 0),
          minutes: du.get('minutes').toString().padStart(2, 0),
          seconds: du.get('seconds').toString().padStart(2, 0),
        }
      } else {
        return false
      }
    },
  },
  methods: {
    getTime(time = '') {
      return this.$moment(time || new Date()).tz('Asia/Shanghai')
    },
    createClock() {
      if (!this.clock) {
        this.clock = setInterval(() => {
          this.startTime = this.getTime()
        }, 1000)
      }
    },
  },
  created() {
    this.createClock()
  },
  mounted() {
    this.$nextTick(() => {
      // 在dom渲染完后,再执行动画
      const wow = new WOW({
        live: false,
      })
      wow.init()
      setTimeout(() => {
        this.loading = true
      }, 1000)
    })
  },
  beforeDestroy() {
    clearInterval(this.clock)
  },
}
</script>
<style scoped>
.loadingHidden {
  display: none !important;
}
.banner-btn {
